<template>
  <div>
    <el-row style="text-align: right;">
      <el-button type="primary" icon="el-icon-download" @click="handleExport">导出</el-button>
    </el-row>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="183"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        :show-operate="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { getInventoryBookHistoryApi, historyRecordExportApi } from '@/apis/modules/warehouse/inventory'
import paginationPage from '@/mixins/paginationPage'
import tableHeadSet from '@/mixins/tableHeadSet'
export default {
  mixins: [paginationPage, tableHeadSet],
  props: {
    materialId: {
      type: [Number, String],
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      columnSets: [
        {
          module: 'link',
          prop: 'billNumber',
          label: '单号',
          align: 'center',
          minWidth: 200,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          disabled: row => !row.stockInOrOutId,
          headerAlign: 'center',
          handleClick: (row, index) => {
            const bool1 = ['1', '3'].includes(row.moveType) // 跳入库单信息页面
            const bool2 = ['2', '4'].includes(row.moveType) // 跳出库单信息页面
            if (bool1) {
              // 跳入库单信息页面
              this.$router.push({
                name: 'InStorageInfoDelivery',
                params: {
                  orderId: row.id, // Tip 订单id
                  id: row.stockInOrOutId // Tip 单号id
                }
              })
            }
            if (bool2) {
              // 跳出库单信息页面
              this.$router.push({
                name: 'OutBoundDelivery',
                params: {
                  billNumber: row.billNumber
                }
              })
            }
            console.log('row', row)
            console.log('index', index)
          }
        }
      ]
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
  },
  activated() {
  },
  methods: {
    init() {
      if (!this.materialId) return
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryBookHistoryApi({
          page: {
            ...this.pageInfo
          },
          materialId: this.materialId,
          warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
    },
    download(result, name) {
      const filename = name
      const blob = new Blob([result.data], { type: 'application/vnd.ms-excel' })
      const url = window.URL.createObjectURL(blob)
      if (window.navigator.msSaveBlob) { // IE
        try {
          window.navigator.msSaveBlob(blob, filename)
        } catch (e) {
          console.log(e)
        }
      } else { // 非IE
        console.log(window.navigator)
        const link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', filename)
        document.body.appendChild(link)
        link.click()
      }
      URL.revokeObjectURL(url) // 释放内存
    },
    handleExport() {
      // this.$notify.warning({
      //   title: '警告',
      //   message: '功能维护中'
      // })
      const params = {
        materialId: this.materialId,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      this.$confirm('是否导出历史记录?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          historyRecordExportApi(params)
            .then(res => {
              this.download(res, '历史记录.xlsx')
            })
            .catch(e => {
              console.log(e)
              this.$notify.error({
                title: '错误',
                message: '导出错误，请重试'
              })
            })
        })
        .catch(err => {
          console.log('err', err)
        })
    }
  }
}
</script>

