<template>
  <div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        :cell-style="handleCellStyle"
        :show-operate="false"
        :operation-width="120"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <!-- 库存台账详情页面取消拆箱功能 -->
        <template slot-scope="scope">
          <el-button type="text" :disabled="scope.row.isCanSplit === 0" @click.native.prevent="handleDevanning(scope.$index, scope.row)">拆箱</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <package-to-s-k-u
      v-model="skuVisible"
      :title="'供应商条码'"
      :is-show-print-btn="false"
      :extra-info="{columnList: columnList1 || [], tableData: dialogInfo1.subBarCodeList || []}"
    />
    <devanning-dialog v-model="devanningvisible" :extra-info="devanningForm" @confirm="init" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { getInventoryBookDetailApi, detailEditApi } from '@/apis/modules/warehouse/inventory'
import paginationPage from '@/mixins/paginationPage'
import tableHeadSet from '@/mixins/tableHeadSet'
// import QueryItemMixin from '@/mixins/queryItem'
import PackageToSKU from '@/components/ModalSelect/PackageToSKU'
import DevanningDialog from '@/components/ModalSelect/Devanning'
import { requestWrapper } from '@/utils/utils'
export default {
  components: {
    DevanningDialog,
    PackageToSKU
  },
  mixins: [paginationPage, tableHeadSet],
  props: {
    materialId: {
      type: [Number, String],
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      devanningvisible: false,
      devanningForm: {},
      columnSets: [
        {
          module: 'input',
          prop: 'remarks',
          label: '备注',
          align: 'center',
          width: 150,
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          handleChange: (item) => this.saveRemarks(item)
        },
        {
          module: 'link',
          type: (item) => Array.isArray(item.subBarCodeList) && !!item.subBarCodeList.length ? 'primary' : '',
          prop: 'barcodeNumber',
          underline: false,
          minWidth: 200,
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          handleClick: (item) => {
            if (!(item.subBarCodeList && item.subBarCodeList.length)) return
            this.skuVisible = true
            this.dialogInfo1 = item
          }
        }
      ], // 自定义表头信息
      columnList1: [],
      skuVisible: false,
      dialogInfo1: {}
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
  },
  activated() {
  },
  methods: {
    init() {
      if (!this.materialId) return
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryBookDetailApi({
          page: {
            ...this.pageInfo
          },
          materialId: this.materialId,
          warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            this.columnList1 = formHeadSets || []
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    async saveRemarks(item) {
      console.log(item)
      const res = await requestWrapper(detailEditApi({ ...item }))
      if (!res.success) return
      this.$message({
        message: '保存成功',
        type: 'success'
      })
    },
    // Tip 拆箱
    handleDevanning(index, row) {
      console.log('row', row)
      this.devanningvisible = true
      this.devanningForm = { ...row }
    },
    // Tip 当前行是否有预警信息，高亮展示
    handleCellStyle({ row, rowIndex }) {
      const cellStyle = {
        backgroundColor: row.earlyWarningInfo ? '#F56C6C' : ''
      }
      return cellStyle
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
    }
  }
}
</script>

