<template>
  <div>
    <el-form ref="queryForm" :model="query" inline>
      <el-form-item class="fr">
        <el-button @click="beforeClose">返回</el-button>
      </el-form-item>
    </el-form>
    <el-descriptions :column="5">
      <el-descriptions-item label="物料编号">{{ materialInfo.code }}</el-descriptions-item>
      <el-descriptions-item label="物料名称">{{ materialInfo.name }}</el-descriptions-item>
      <el-descriptions-item label="物料组">{{ materialInfo.materialGroupCode }}</el-descriptions-item>
      <el-descriptions-item label="评估类型">{{ materialInfo.assessType_dictText }}</el-descriptions-item>
    </el-descriptions>
    <el-tabs v-model="activeName">
      <el-tab-pane label="详细信息" name="info">
        <info v-if="activeName === 'info'" :material-id="materialId" />
      </el-tab-pane>
      <el-tab-pane label="历史记录" name="record">
        <record v-if="activeName === 'record'" :material-id="materialId" />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { getBaseInfoApi } from '@/apis/modules/warehouse/inventory'
import { Info, Record } from './components'
export default {
  name: 'WarehouseInventoryDetail',
  components: {
    Info,
    Record
  },
  props: {
    materialId: {
      type: [Number, String],
      default: ''
    }
  },
  data() {
    return {
      query: {},
      activeName: 'info',
      materialInfo: {}
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      if (!this.materialId) return
      return new Promise((resolve, reject) => {
        getBaseInfoApi({
          id: this.materialId
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.materialInfo = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    beforeClose() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({ name: 'WarehouseInventory' })
    }
  }
}
</script>

